.statistics {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
  width: 100%;
  border-radius: 15px;
  background-color: white;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}
.title {
  margin-top: 25px;
  margin-bottom: 25px;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 30px;
  color: grey;
  text-transform: uppercase;
}
.stat-list {
  display: flex;
  border-radius: 15px;
  width: 100%;
}
